@use "sass:color";

// Elevation 0
$md-sys-elevation-level0-opacity: 0%;

@mixin md-sys-elevation-level0-box-shadow($color) {
  box-shadow: unset;
}

// Elevation 1
$md-sys-elevation-level1-opacity: 5%;

@mixin md-sys-elevation-level1-box-shadow($color) {
  box-shadow: 0 .0625rem .125rem 0 color.adjust($color, $alpha: -.7), 0 .0625rem .1875rem .0625rem color.adjust($color, $alpha: -.85);
}

// Elevation 2
$md-sys-elevation-level2-opacity: 8%;

@mixin md-sys-elevation-level2-box-shadow($color) {
  box-shadow: 0 .0625rem .125rem 0 color.adjust($color, $alpha: -.7), 0 .125rem .375rem .125rem color.adjust($color, $alpha: -.85);
}

// Elevation 3
$md-sys-elevation-level3-opacity: 11%;

@mixin md-sys-elevation-level3-box-shadow($color) {
  box-shadow: 0 .0625rem .1875rem 0 color.adjust($color, $alpha: -.7), 0 .25rem .5rem .1875rem color.adjust($color, $alpha: -.85);
}

// Elevation 4
$md-sys-elevation-level4-opacity: 12%;

@mixin md-sys-elevation-level4-box-shadow($color) {
  box-shadow: 0 .125rem .1875rem 0 color.adjust($color, $alpha: -.7), 0 .375rem .625rem .25rem color.adjust($color, $alpha: -.85);
}

// Elevation 5
$md-sys-elevation-level5-opacity: 14%;

@mixin md-sys-elevation-level5-box-shadow($color) {
  box-shadow: 0 .25rem .25rem 0 color.adjust($color, $alpha: -.7), 0 .5rem .75rem .375rem color.adjust($color, $alpha: -.85);
}
